// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import axios from 'axios'
Vue.prototype.$http = axios

import Global from '@/components/common/Global'
Vue.prototype.$global = Global

//   
Vue.prototype.getUserConfig = function(){
	this.$http.get("api-h5/user/config/queryUserConfig",{
      params:{
         token:'3243a798a09523dae2aff9b0715466c3'
      }
    }).then(function(response){
          if(response.data.success){
              //
              const data = response.data.data;
              console.log(data);

              const bannedTime = data.bannedTime //
              localStorage.setItem('bannedTime',bannedTime)

              const bannedDurationM = data.bannedDurationM //
              localStorage.setItem('bannedDurationM',bannedDurationM)

              const memberTime = data.memberTime //
              localStorage.setItem('memberTime',memberTime)

              const memberDurationD = data.memberDurationD //
              localStorage.setItem('memberDurationD',memberDurationD)

              const bannedLastMinutes = data.bannedLastMinutes //
              localStorage.setItem('bannedLastMinutes',bannedLastMinutes)

              const isMember = data.isMember //
              localStorage.setItem('isMember',isMember);
          }
      })
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  methods: {
    getToken () {
      let url = this.$global.ApiObj.getToken
      this.$http.get(url, {
        params: {
          accountId: '11'
        }
      })
      .then(function (response) {
        window.localStorage.setItem('token', response.data.data)
      })
      .catch(function (response) {
        console.log(response)
      })
    }
  },
  mounted () {
    this.getToken()
  }
})



