import Vue from 'vue'
import Router from 'vue-router'
import DisCover from '@/components/index' //
import Study from '@/components/study/study' //
import Particulars from '@/components/study_detail/particulars' //
import Zhibo from '@/components/zhibo/zhibo' //
import Index_detail from '@/components/index_detail/index_detail' //


import Mine from '@/components/mine/mine' //



import Write from '@/components/write/write'
import Tag from '@/components/tag/Tag'
import UnfinishedWork from '@/components/unfinishedWork/UnfinishedWork'

import systemMessage from '@/components/systemmessage/systemmsg' //
import MyPraise from '@/components/mypraise/MyPraise' //
import Discuss from '@/components/discuss/discuss' //--
import Message from '@/components/message/message' //--
import Feedback from '@/components/feedback/feedback'  //


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'DisCover',
      component: DisCover
    },
    {
      path: '/study',
      name: 'Study',
      component: Study
    },
    {
      path: '/study_detail',
      name: 'Particulars',
      component: Particulars
    },
    {
      path: '/index_detail',
      name: 'Index_detail',
      component: Index_detail
    },
    {
      path: '/zhibo',
      name: 'Zhibo',
      component:Zhibo
    },
    {
      path: '/write',
      name: 'Write',
      component: Write
    },
    {
      path: '/write/:id/:name/:tag/:arttype/:artid',
      name: 'WriteArt',
      component: Write
    },
    {
      path: '/tag',
      name: 'Tag',
      component: Tag
    },
    {
      path: '/unfinishedwork',
      name: 'UnfinishedWork',
      component: UnfinishedWork
    },
    {
      path: '/message',
      name: 'Message',
      component: Message
    },
    {
      path: '/mypraise',
      name: 'MyPraise',
      component: MyPraise

    },
    {
      path: '/systemmessage',
      name: 'systemMessage',
      component: systemMessage
    },
    {
      path: '/discuss',
      name: 'Discuss',
      component: Discuss
    },
    {
      path: '/mine',
      name: 'Mine',
      component: Mine
    },
    {
      path: '/feedback',
      name: 'Feedback',
      component: Feedback
    }
  ]
})



